@-webkit-keyframes borderEffect {
  0% {
    width: 0vh;
    height: 0vh;
    border-top: 0px solid #9aff00;
    border-radius: 0%;
    border-bottom-left-radius: 0%;
  }
  33% {
    width: 0vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-radius: 0%;
  }
  66% {
    width: 30vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-bottom: 1px solid #9aff00;
    border-radius: 0%;
  }
  100% {
    width: 30vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-bottom: 1px solid #9aff00;
    border-right: 1px solid #9aff00;
